import React, { useState } from 'react'
import styles from './profile.module.css'
import profilePhoto from '../../assets/imgs/profile.png'
import chev from '../../assets/icons/Chevron-Right.png'
import hamburgerMenu from '../../assets/icons/Hamburger-Menu.png'
import hamburgerMenuToggled from '../../assets/icons/Hamburger-Menu-Toggled.png'
import horizontalEllipsis from '../../assets/icons/Horizontal-Ellipsis.png'

function ProfileComponent(props) {
  const [sidebarState, setSidebarState] = useState(false)

  const toggleSidebar = () => {
    setSidebarState(!sidebarState)
    props.toggleSidebar(!sidebarState)
  }

  return (
    <>
      <div className="row mb-4 pb-1">
        <div className="col-12">
          {sidebarState ? (
            <img
              src={hamburgerMenuToggled}
              alt="HamburgerMenuToggledIcon"
              className="mx-4"
              onClick={toggleSidebar}
              style={{ cursor: 'pointer' }}
            />
          ) : (
            <img
              src={hamburgerMenu}
              alt="HamburgerMenuIcon"
              className="float-end"
              onClick={toggleSidebar}
              style={{ cursor: 'pointer' }}
            />
          )}
        </div>
      </div>
      <div className="mb-3 px-3 d-flex position-relative">
        <div className="position-relative">
          <div
            className={`${styles.vaiNotification} ${
              sidebarState ? styles.vaiShrinkNotification : null
            }`}
          >
            <span>2</span>
          </div>
          <span
            className={`${styles.vaiStatus} ${
              sidebarState ? styles.vaiShrinkStatus : null
            }`}
          ></span>
          <img
            src={profilePhoto}
            className={`${styles.vaiProfilePhoto} ${
              sidebarState ? styles.vaiShrinkPhoto : null
            }`}
            alt="ProfilePhoto"
          />
        </div>
        <div
          className={`${
            sidebarState ? styles.vaiHide : null
          } position-relative`}
          style={{ marginLeft: '15px' }}
        >
          <p className={styles.vaiUsername}>
            Ronald Madrid
            <br /> <span className={styles.vaiUserTitle}>.NET Developer</span>
          </p>
        </div>
        <img
          src={chev}
          alt="ChevUpIcon"
          className={`${styles.vaiChevUp} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
        <img
          src={chev}
          alt="ChevDownIcon"
          className={`${styles.vaiChevDown} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
      </div>
      <div className="pb-3 text-center">
        <img
          src={horizontalEllipsis}
          style={{ display: sidebarState ? null : 'none', marginTop: '-20px' }}
          alt="Horizontal-Ellipsis"
        />
      </div>
    </>
  )
}

export default ProfileComponent
