import React, { useState } from 'react'
import styles from '../login.module.css'
import VirtualaiLogo from '../../../assets/icons/VirtualAiLogo.png'
import signInIcon from '../../../assets/icons/Sign-In.png'
import eyeIcon from '../../../assets/icons/Eye.png'
import { useHistory } from 'react-router-dom'

function SignInComponent(props) {
  const history = useHistory()
  const [showPassword, setShowPassword] = useState('password')

  const handleLogin = () => {
    let path = `/encore-monitoring`
    history.push(path)
  }

  const showForgotPasswordComponent = () => {
    props.showForgotPasswordComponent()
  }

  const togglePassword = () => {
    if (showPassword === 'password') {
      setShowPassword('text')
    } else {
      setShowPassword('password')
    }
  }

  return (
    <>
      <img
        src={VirtualaiLogo}
        alt="VirtualAiLogo"
        className={styles.vaiLoginLogo}
      />
      <p className={styles.vaiTitleText}>Sign In</p>
      <input
        type="text"
        className={styles.vaiLoginEmailInput}
        placeholder="Email"
      />
      <br />
      <div className="input-group position-relative">
        <input
          type={showPassword}
          className={styles.vaiLoginPasswordInput}
          placeholder="Pasword"
          aria-label="Pasword"
          aria-describedby="password-addon"
        />
        <div
          className={`${styles.vaiLoginPasswordInputIcon} input-group-append`}
        >
          <img
            src={eyeIcon}
            alt="EyeIcon"
            style={{ cursor: 'pointer' }}
            onClick={togglePassword}
          />
        </div>
      </div>
      <button className={styles.vaiSignInButton} onClick={handleLogin}>
        Sign In <img src={signInIcon} className={styles.vaiSignInButtonIcon} />
      </button>
      <p
        className={styles.vaiForgotPasswordText}
        onClick={showForgotPasswordComponent}
        style={{ cursor: 'pointer' }}
      >
        Forgot password?
      </p>
    </>
  )
}

export default SignInComponent
